<template>
    <div class="black-bg" v-if="모달창열렸니 == true">
      <div class="white-bg">
        <img :src="원룸들[누른거].image" style="width: 100%">
        <h4>{{ 원룸들[누른거].title }}</h4>
        <p>{{ 원룸들[누른거].content }}</p>

        <input v-model="month" type="range" min="1" max="24">
        <input v-model="month">  <!-- @input="month = $event.target.value" --> <!-- v-model.number="month" -->
        <p v-if="noinput == false"> {{month}}개월 선택함: {{ 원룸들[누른거].price * month }} 원</p>
        <p v-if="noinput == true"> 0개월 선택함: 0원</p>
        
        <button @click="month=1; $emit('closeModal')">닫기</button>
      </div> 
    </div>
</template>


<script>

export default {
    name: 'ModalComponent',
    data() {
        return {
          month: 1,
          noinput: false,
        };
    },
    props: {
      원룸들 : Array,
      누른거 : Number,
      모달창열렸니 : Boolean,
    },

    beforeUpdate() {
      if(this.month == 2) {
        alert('2개월은 너무 적음. 3개월부터 가능.');
      }
    },

    components: {
    },
    watch: {
      month(num, numOld) {   //(num1, num2) => num1 is new input, num2 is old input
        if(isNaN(num) == true) {
          alert('숫자만 입력하세요.');
          this.month = 1;
        }
        if(num > 24) {
          alert('24 이상 입력하지 마세요.');
          this.month = numOld;
        }
        if(num == '') {
          this.noinput = true;
        }
        else {
          this.noinput = false;
        }
      },
    },
}

</script>

