<template>
  <div>

    <div class="start" :class="{ end : 모달창열렸니 }">
      <ModalComponent @closeModal="모달창열렸니=false;" :원룸들="원룸들" :누른거="누른거" :모달창열렸니="모달창열렸니"/>
    </div>

    <div class="menu">
      <a v-for="작명 in 메뉴들" :key="작명">{{ 작명 }}</a>
    </div>

    <DiscountComponent v-if="showDiscount == true" :min="min" :sec="sec"/>
    <!--
    <div class="discount" v-if="showDiscount == true">
      <h3>{{ min }}분 {{ sec }}초 안에 결제하면 20% 할인</h3>
    </div>
    -->

    <img src="./assets/logo.png" alt="Logo image" class="logo-img">
    <h1 class="budongsan-name">Dream Budongsan</h1><br><br>

    <div id="outer" v-if="모달창열렸니 == false">
      <button @click="priceAscending" class="inner button-asc">가격순정렬</button>
      <button @click="priceDescending" class="inner">가격역순정렬</button>
      <button @click="sortAbc" class="inner button-abc">가나다순정렬</button>
      <button @click="priceRevert" class="inner button-revert">되돌리기</button>
      <button @click="priceFilter" class="inner button-filter">50만원 이하</button>
    </div>
    
    <CardComponent @openModal="모달창열렸니=true; 누른거=$event" :원룸="원룸들[i]" v-for="(r, i) in 원룸들" :key="i" />
    
    <!--
    <div v-for="(r, i) in 원룸들" :key="i" class="room-text">
      <img :src="원룸들[i].image" alt="Room image" class="room-img">
      <h4 @click="모달창열렸니 = true" class="mouse-over-underline">{{ r.title }}</h4>
      <p>{{ r.price }} 만원</p>
      <button @click="increase(i)">허위매물신고</button>
      <p>신고수: {{ 신고수[i] }}</p>
    </div>
    -->

  </div>
</template>


<script>

import data from "./assets/oneroom.js";
import DiscountComponent from "./components/DiscountComponent.vue";
import ModalComponent from "./components/ModalComponent.vue";
import CardComponent from "./components/CardComponent.vue";

export default {
  name: 'App',
  data() {
    return {
      원룸들오리지널: [...data],
      원룸들: data,
      모달창열렸니: false,
      products: ['역삼동원룸', '천호동원룸', '마포구원룸'],
      prices: [50, 60, 70],
      메뉴들: ['Home', 'Shop', 'About'],
      신고수: [0, 0, 0, 0, 0, 0],
      누른거: 0,
      showDiscount: true,
      min: 2,
      sec: 59,
    };
  },

  methods: {
    increase(i) {
      const currentQuantity = this.신고수[i];
      const newQuantity = currentQuantity + 1;
      //replace old quantity with new one
      this.신고수.splice(i, 1, newQuantity);
    },
    priceAscending() {
      this.원룸들.sort(function(a, b) {
      return a.price - b.price;  });
    },
    priceDescending() {
      this.원룸들.sort(function(a, b) {
        return b.price - a.price;  });
    },
    priceRevert() {
      this.원룸들 = [...this.원룸들오리지널];
    },
    sortAbc() {
     this.원룸들.sort(function(a, b) {
        if(a.title < b.title) {
          return -1;
        }
        if(a.title > b.title) {
          return 1;
        }
        return 0;  });
    },
    priceFilter() {
      var newArr = [];
      for(let i=0; i<6; i++) {
        if(this.원룸들[i].price < 500000) {
          newArr.push(this.원룸들[i]);   }
      }
      return this.원룸들 = newArr;
    },
  },

  //lifecycle hook
  //할인 남은 시간
  mounted() {
    setInterval(() => {    //setInterval(function()
      this.sec--;
      if(this.min == 0 & this.sec == -1) {
        this.showDiscount = false;
      }
      if(this.sec == -1) {
        this.min--;
      this.sec = 59;
      }
    }, 1000);
  },

  components: {
    DiscountComponent : DiscountComponent,
    ModalComponent : ModalComponent,
    CardComponent : CardComponent,
  },
};
</script>


